<template>
    <div class="div_forDifferentTabs content wcb" id="div_ActivityOuterDiv" style="">
        <div style="position: relative;" id="activityPanel">
            <div class="scroll-wrapper act scrollbar-inner scroll-content scroll-scrolly_visible"
                style="position: relative;">
                <div class="act scrollbar-inner scroll-content scroll-scrolly_visible"
                    style="height: auto; margin-bottom: 0px; margin-right: 0px; "
                    id="pills-tabContent">
                        <div class="ball-loader" v-if="isDataLoaded">
                        <div class="ball-loader-ball ball1">
                        </div>
                        <div class="ball-loader-ball ball2">
                        </div>
                        <div class="ball-loader-ball ball3">
                        </div>
                    </div>
                    <div id="search-forward-error" class="text-center text-danger border p-2" v-if="noRecord">{{$t('NoRecordfound') }}</div>
                    <div class="activity_bx card-contact files-tab" v-for="(CALLER_NAME, key) in Filterdate" :key="key" v-else>
                        <h4>{{ formatDates($t(key)) }} </h4>
                        <div class="card-list  last" v-for="(item, index) in CALLER_NAME" :key="index">
                            <div class="circle-bx green_bg" v-if="item.CALL_TYPE=='Outgoing' || item.CALL_TYPE=='Incomming'"><i class="fa fa-phone"></i></div>
                            <div class="circle-bx green_bg" v-if="item.CALL_TYPE=='CHAT'"><i class="fa fa-comments"></i></div>
                            <div class="card-desc" v-if="item.CALL_TYPE=='Outgoing' || item.CALL_TYPE=='Incomming'">
                                <p class="sm">Call</p>
                                <p>User call with {{ item.CALL_TO  }} Duration {{ item.DURATION ? item.DURATION : 0  }}</p>
                            </div>
                            <div class="card-desc" v-if="item.CALL_TYPE=='CHAT'">
                                <p class="sm">User Chat</p>
                                <p>User chat with {{ item.CALLER_NAME }}</p>
                            </div>
                            <div class="card-icon">{{ $options.filters.formatTime(item.FOR_SORT) }}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>



</template>

<script>
import DataService from '../services/DataService';
export default
    {
        props: {
            FromDate: {
            type: String,
        },
        ToDate: {
            type: String
        },
    },
        data() {
            return {
                isDataLoaded:false,
                ActivityData: [],
                RecNumber: 50,
                type: 'web',
                sender: {},
                Filterdate: [],
                is_first_time: 1,
                lastrow: 0,
                noRecord:false,
            }
        },

        created: async function () {
             
            this.ToDate;
            this.FromDate;
            var vm = this;
            var obj = {
                
                companyId: vm.GetUserInfo.CompanyId,
                emaiId: vm.GetUserInfo.Email,
                groupName: '',
                id: vm.GetUserInfo.ID,
                imgPath: 'https://cogniterblob.blob.core.windows.net/cg-drish-info-tech-270936/Upload/UserProfileImage/DAaFVeiLcHphoto-1633332755192-727a05c4013d.jpg',
                is_first_time: 1,
                isGlobalSearch: false,
                lastchatId: null,
                lastrow: 0,
                name: vm.GetUserInfo.Name,
                recordNumber: vm.RecNumber,
                searchForMsg: null,
                type: vm.type,
                FromDate: vm.FromDate,
                ToDate: vm.ToDate
            }
            vm.sender = obj;
            await vm.BindActivity();

        },
        watch:{
            FromDate(){
                debugger
                var vm = this;
                var obj = {
                companyId: vm.GetUserInfo.CompanyId,
                emaiId: vm.GetUserInfo.Email,
                groupName: '',
                id: vm.GetUserInfo.ID,
                imgPath: 'https://cogniterblob.blob.core.windows.net/cg-drish-info-tech-270936/Upload/UserProfileImage/DAaFVeiLcHphoto-1633332755192-727a05c4013d.jpg',
                is_first_time: 1,
                isGlobalSearch: false,
                lastchatId: null,
                lastrow: 0,
                name: vm.GetUserInfo.Name,
                recordNumber: vm.RecNumber,
                searchForMsg: null,
                type: vm.type,
                FromDate: vm.FromDate,
                ToDate: vm.ToDate
            }
            vm.sender = obj;
                this.BindActivity();
            }
        },

        
        methods:
        {
            formatDates(dateString) {
             
            const options = { month: 'short', day: 'numeric', year: 'numeric' };
            const date = new Date(dateString);
            return new Intl.DateTimeFormat('en-US', options).format(date);
        },
        async BindActivity() {
            var vm = this; 
            vm.isDataLoaded = true;
            vm.noRecord = false;
            vm.Filterdate = []
            let url=''
            if(vm.ToDate == null && vm.FromDate == null){
            url = `emaiId=${vm.GetUserInfo.Email}&is_first_time=${vm.is_first_time}&lastrow=${vm.lastrow}&recordNumber=${vm.RecNumber}&FromDate=&ToDate=`;

            }
            else {
            url = `emaiId=${vm.GetUserInfo.Email}&is_first_time=${vm.is_first_time}&lastrow=${vm.lastrow}&recordNumber=${vm.RecNumber}&FromDate=${vm.FromDate}&ToDate=${vm.ToDate}`;

            }
            await DataService.GetAllActivity(url).then(response => {
                if (response.data =='[]'? null :  response.data != null) {
                    debugger
                    if (response.data.length > 0) {
                        vm.ActivityData = response.data;  
                            let group = {};
                            const ActivitydataArray= JSON.parse(vm.ActivityData) 
                            ActivitydataArray.forEach(item => {
                            let data = item.CALL_TIME.substring(0, 10);
                            if (group[data]) {
                                group[data].push(item);
                            } 
                            else {
                                group[data] = [];
                                group[data].push(item);
                            }
                            vm.Filterdate = group;
                        });
                        vm.noRecord = false;
                    } 
                } 
                else{vm.noRecord = true;}
            });
            vm.isDataLoaded = false;
        }
            // handleDatePass:function(data){
                 
            // }
        }
    }

</script>
