        /**
         * End to end streaming
         * @param {*} config
         */
        sessionDescription: function (config) {
            const peer_id = config.peer_id;
            if (this.peers) {
                console.log("checkk")
                const peer = this.peers[peer_id];
                const remote_description = config.session_description;
                const desc = new RTCSessionDescription(remote_description);
                if (peer) {
                    console.log("checkk22")
                    peer.setRemoteDescription(desc)
                        .then(() => {
                            if (remote_description.type === "offer") {
                                return peer.createAnswer();
                            }
                        })
                        .then((local_description) => {
                            return peer.setLocalDescription(local_description);
                        })
                        .then(() => {
                            this.$socket.emit('relaySessionDescription', {
                                peer_id: peer_id,
                                session_description: peer.localDescription,
                            });
                        })
                        .catch((error) => {
                            console.log("Error setting remote description or creating answer: ", error);
                        });
                }
            }

        },
        /**
         * generate ICE candidate for messaging
         * @param {*} data
         */
        iceCandidate: function (data) {

            console.log("iceCandidate", data);
            var peer = this.peers[data.peer_id];
            var ice_candidate = data.ice_candidate;
            peer.addIceCandidate(new RTCIceCandidate(ice_candidate));
        },
        /**
         * Handle rejecting a video call
