<template>
    <section>

        <!-- Start chats tab-pane -->
        <div class="tab-pane fade show active" id="pills-chat" role="tabpanel" aria-labelledby="pills-chat-tab">
            <!-- Start chats content -->
            <div>
                <div class="px-3 pt-3">
                    <h4 class="mb-4 d-flex justify-content-between">Chats <i class="ri-add-line"></i></h4>
                    <div class="search-box chat-search-box">
                        <div class="input-group mb-3 rounded-3">
                            <input type="text" class="form-control bg-light" v-model="txtSearchChatGroups"
                                id="UserSearch" v-on:keyup="SearchChatUsers($event)" placeholder="Search"
                                aria-label="Search messages or users" aria-describedby="basic-addon1">
                            <span class="input-group-text text-muted bg-light pe-3 ps-3 border-0" id="basic-addon1">
                                <i class="ri-search-line search-icon font-size-18"></i>
                            </span>
                        </div>
                    </div> <!-- Search Box-->
                </div> <!-- .p-4 -->

                <!-- Start user status -->
                <div class="px-3 pb-4" dir="ltr">

                    <ul class="nav nav-tabs customtabs" id="myTab" role="tablist">
                        <li class="nav-item" role="presentation" @click="selectTab('AllUsers')">
                            <button class="nav-link" :class="{ 'active': activeTabs == 'AllUsers' }" id="allchat-tab"
                                data-bs-toggle="tab" data-bs-target="#allchat" type="button" role="tab"
                                aria-controls="allchat" aria-selected="true">All Chats</button>
                        </li>
                        <li class="nav-item" role="presentation" @click="selectTab('Group')">
                            <button class="nav-link" id="group-tab" :class="{ 'active': activeTabs == 'Group' }"
                                data-bs-toggle="tab" data-bs-target="#group" type="button" role="tab"
                                aria-controls="group" aria-selected="false">Group</button>
                        </li>
                        <!-- <li class="nav-item" role="presentation" @click="selectTab('Pinned')">
                        <button class="nav-link" id="pinned-tab" :class="{'active':activeTabs=='Pinned'}" data-bs-toggle="tab" data-bs-target="#pinned" type="button" role="tab" aria-controls="pinned" aria-selected="false">Pinned</button>
                    </li> -->
                        <li class="nav-item" role="presentation" @click="selectTab('Archived')">
                            <button class="nav-link" id="archived-tab" :class="{ 'active': activeTabs == 'Archived' }"
                                data-bs-toggle="tab" data-bs-target="#archived" type="button" role="tab"
                                aria-controls="archived" aria-selected="false">Archived</button>
                        </li>
                    </ul>
                    <!-- end user status carousel -->
                </div>
                <!-- end user status -->

                <!-- Start chat-message-list -->
                <div class="">

                    <div class="tab-content" id="myTabContent">
                        <div class="tab-pane fade show " :class="{ 'active': activeTabs == 'AllUsers' }" id="allchat"
                            role="tabpanel" aria-labelledby="allchat-tab">
                            <div class="chat-message-list px-2" data-simplebar>
                                <ul class="list-unstyled chat-list chat-user-list">
                                    <li v-for="userItems, index in ChatUserListData" :key="index"
                                        :class="{ 'active': chatUserInfo.GROUP_ID == userItems.GROUP_ID, 'unread notseen': userItems.UNREAD_USER_MESSAGE > 0, 'typing': isShowTypingForLeft && userItems.GROUP_ID == isTypingToGroup }">
                                        <a v-on:click="JoinRooms(userItems)">
                                            <div class="d-flex">
                                                <div class="chat-user-img align-self-center me-3 ms-0"
                                                    :class="{ 'online': userItems.IsOnlineUser == 1 && userItems.IS_GROUP != true, 'offline': userItems.IsOnlineUser == 0 && userItems.IS_GROUP != true }">
                                                    <img v-if="userItems.hasOwnProperty('GROUP_ID') && userItems.GROUP_ID && userItems.AVATAR != ''"
                                                        :src="userItems.AVATAR ? userItems.AVATAR : ''"
                                                        class="rounded-circle avatar-xs" alt="">
                                                    <img v-else src="../assets/images/group-user.jpg"
                                                        class="rounded-circle avatar-xs" alt="">
                                                    <span class="user-status"></span>
                                                </div>
                                                <div class="flex-grow-1 overflow-hidden">
                                                    <h5 class="text-truncate font-size-14 mb-1">{{ userItems.GROUP_NAME
                                                        }}</h5>
                                                    <p class="chat-user-message text-truncate mb-0"
                                                        :class="{ 'animate-typing': isShowTypingForLeft && userItems.GROUP_ID == isTypingToGroup }">
                                                        {{ isShowTypingForLeft && userItems.GROUP_ID == isTypingToGroup
                                == true ? 'typing' : (typeof userItems.MESSAGE === 'string' &&
                                    userItems.MESSAGE.includes('src') &&
                                    userItems.hasOwnProperty('GROUP_ID') != false) ? '[GIF]' :
                                userItems.MESSAGE }}
                                                        <span
                                                            v-if="isShowTypingForLeft && userItems.GROUP_ID == isTypingToGroup"
                                                            class="dot"></span>
                                                        <span
                                                            v-if="isShowTypingForLeft && userItems.GROUP_ID == isTypingToGroup"
                                                            class="dot"></span>
                                                        <span
                                                            v-if="isShowTypingForLeft && userItems.GROUP_ID == isTypingToGroup"
                                                            class="dot"></span>
                                                    </p>

                                                </div>
                                                <div class="unread-message">
                                                    <span class="badge badge-soft-info rounded-pill"
                                                        v-if="userItems.UNREAD_USER_MESSAGE > 0">{{
                                userItems.UNREAD_USER_MESSAGE }}</span>
                                                </div>
                                                <!-- <p class="ut d-none"><img :src="leftIsTyping"></p> -->

                                                <div class="font-size-11" v-if="userItems.hasOwnProperty('CREATED_AT')">
                                                    {{ DateDifference(userItems.CREATED_AT) }}
                                                    <span class="read d-block text-center mt-2">
                                                        <i class="ri-check-double-line font-size-15"></i></span>
                                                </div>
                                            </div>
                                        </a>
                                    </li>

                                </ul>
                            </div>

                        </div>
                        <div class="tab-pane fade show" :class="{ 'active': activeTabs == 'Group' }" id="group"
                            role="tabpanel" aria-labelledby="group-tab">
                            <div class="chat-message-list px-3" data-simplebar>

                                <ul class="list-unstyled chat-list chat-user-list">

                                    <li class="active" v-for="(group, groupIndex) in groupRecords"
                                        v-bind:key="groupIndex">
                                        <a href="#" v-on:click="JoinRooms(group)">
                                            <div class="d-flex">
                                                <div class="chat-user-img align-self-center me-3 ms-0">
                                                    <img v-if="group.hasOwnProperty('GROUP_ID') && group.GROUP_ID && group.AVATAR != ''"
                                                        :src="group.AVATAR ? group.AVATAR :''"
                                                        class="rounded-circle avatar-xs" alt="">
                                                    <img v-else src="../assets/images/group-user.jpg"
                                                        class="rounded-circle avatar-xs" alt="">
                                                </div>
                                                <div class="flex-grow-1 overflow-hidden">
                                                    <h5 class="text-truncate font-size-14 mb-1">{{ group.GROUP_NAME }}
                                                    </h5>
                                                    <p class="chat-user-message text-truncate mb-0">Ben <i
                                                            class="fas fa-camera"></i> {{ group.MESSAGE }}</p>
                                                </div>
                                                <div class="unread-message">
                                                    <span class="badge badge-soft-info rounded-pill"
                                                        v-if="group.UNREAD_USER_MESSAGE > 0">{{
                                                        group.UNREAD_USER_MESSAGE }}</span>
                                                </div>
                                                <div class="font-size-11">{{ DateDifference(group.CREATED_AT) }} <span
                                                        class="read d-block text-center mt-2"><i
                                                            class="ri-check-double-line font-size-15"></i></span></div>
                                            </div>
                                        </a>
                                    </li>
                                </ul>
                            </div>

                        </div>
                        <div class="tab-pane fade" :class="{'active':activeTabs=='Pinned'}" id="pinned" role="tabpanel"
                            aria-labelledby="pinned-tab">
                            <ul class="list-unstyled chat-list chat-user-list">

                                <li>
                                    <a href="#">
                                        <div class="d-flex">
                                            <div class="chat-user-img align-self-center me-3 ms-0">
                                                <div class="avatar-xs">
                                                    <span class="avatar-title rounded-circle bg-red-subtle ">
                                                        R
                                                    </span>
                                                </div>
                                            </div>
                                            <div class="flex-grow-1 overflow-hidden">
                                                <h5 class="text-truncate font-size-14 mb-1">Rattie Bush</h5>
                                                <p class="chat-user-message text-truncate mb-0">Hi, I'm happy to join
                                                </p>
                                            </div>
                                            <div class="font-size-11">15 min</div>
                                        </div>
                                    </a>
                                </li>

                                <li>
                                    <a href="#">
                                        <div class="d-flex">
                                            <div class="chat-user-img online align-self-center me-3 ms-0">
                                                <img src="assets/images/users/avatar-4.jpg"
                                                    class="rounded-circle avatar-xs" alt="">
                                                <span class="user-status"></span>
                                            </div>
                                            <div class="flex-grow-1 overflow-hidden">
                                                <h5 class="text-truncate font-size-14 mb-1">Virgie Arnold</h5>
                                                <p class="chat-user-message text-truncate mb-0">Hi, I'm happy to join
                                                </p>
                                            </div>
                                            <div class="font-size-11">15m</div>
                                        </div>
                                    </a>
                                </li>

                            </ul>

                        </div>
                        <div class="tab-pane fade" :class="{'active':activeTabs=='Archived'}" id="archived"
                            role="tabpanel" aria-labelledby="archived-tab">
                            <ul class="list-unstyled chat-list chat-user-list">

                                <li>
                                    <a href="#">
                                        <div class="d-flex align-items-center">
                                            <div class="chat-user-img offline align-self-center me-3 ms-0">
                                                <img src="assets/images/users/avatar-6.jpg"
                                                    class="rounded-circle avatar-xs" alt="">
                                                <span class="user-status"></span>
                                            </div>
                                            <div class="flex-grow-1 overflow-hidden">
                                                <h5 class="text-truncate font-size-14 mb-1">Derrick Webster</h5>
                                            </div>

                                        </div>
                                    </a>
                                </li>

                            </ul>

                        </div>
                    </div>

                </div>
                <!-- End chat-message-list -->
            </div>
            <!-- Start chats content -->
        </div>
        <!-- End chats tab-pane -->

    </section>
</template>


<script>
import DataService from '../services/DataService'
export default {
    name: "LeftSideChatTab",
    components: {

    },
    props: {
        store: {
            type: Object
        },
        // group:{

        // }
    },
    data() {
        return {
            ChatUserListData: [],
            chatHistoryDetails: [],
            chatUsersList: [],
            isDataLoaded: true,
            isMobile: false,
            AllContactList: [],
            chatUserInfo: {},
            GroupAliasName: '',
            tmpChatUserList: [],
            userGroupId: '',
            activeTabs: 'AllUsers',
            sender: this.store.GetSenderDetail,
            txtSearchChatGroups: '',
            groupRecords: [],
            isShowTypingForLeft: false,
            group: "-1",
        }
    },
    sockets: {
        connect() {
            console.log('Connected to server');
        },
        disconnect() {
            console.log('Disconnected from server');
        },
        // Handle receiving the list of chat log users
        historyLogUsers: async function (msg) {
            this.ChatUserListData = [];
            this.historyLogUsers(msg)
        },
        connectedSockets: function (activeUsers) {

            this.ChatUserListData = this.ChatUserListData.map(v => ({
                ...v,
                IsOnlineUser: 0
            }))
            activeUsers.forEach((itm) => {

                var users = this.ChatUserListData.findIndex(x => x.RECEIVER_ID == itm.ID.toString());
                if (users != undefined && users != -1) {
                    this.ChatUserListData[users].IsOnlineUser = 1;
                }

            })
        },
        // Handle typing indicator display
        display: function (data) {

            var vm = this;
            let CurrnetChat = ''
            if (data.typing == true) {
                if (data.IsGroup != true) {
                    if (data.Group == vm.userGroupId) {
                        CurrnetChat = this.ChatUserListData.find(x => x.GROUP_ID == data.Group.toString());
                        vm.isTypingToGroup = CurrnetChat.GROUP_ID;
                        if (CurrnetChat != null && data.user != vm.sender.id) {
                            vm.isShowTypingForLeft = true;
                            setTimeout(function () {
                                vm.isTypingToGroup = '';
                                vm.isShowTypingForLeft = false;
                            }, 4000);

                        } else {
                            vm.isShowTypingForLeft = false;
                        }

                    } else {
                        CurrnetChat = this.ChatUserListData.find(x => x.GROUP_ID == data.Group.toString());
                        vm.isTypingToGroup = CurrnetChat.GROUP_ID;
                        vm.isShowTypingForLeft = true;
                    }

                } else if (data.IsGroup == true) {

                    if (data.user != vm.sender.id) {
                        vm.isShowTypingForLeft = true;
                        CurrnetChat = this.ChatUserListData.find(x => x.GROUP_ID == data.Group.toString());
                        vm.isTypingToGroup = CurrnetChat.GROUP_ID;
                        setTimeout(function () {

                            vm.isShowTypingForLeft = false;
                        }, 4000);
                    }
                }

            } else {

                vm.isShowTypingForLeft = false;
            }
            setTimeout(function () {
                vm.isTypingToGroup = '';
                vm.isShowTypingForLeft = false;
            }, 4000);
        },
        // // Handle tab blink notification for new messages
        // tabBlinkNewMessage: async function (data) {

        // debugger
        // var vm = this;
        // var userLoggedIn = vm.chatUsersList.find(x => x.GROUP_ID == data.roomId);
        // vm.isUserLoggedInId = userLoggedIn.GROUP_NAME_ALIAS.split('_')
        // // if (vm.$refs.ChatsTab.classList.contains('active') == false) {
        // //     vm.blinkUserId = vm.sender.id;
        // //     vm.isUserLoggedInId.forEach((itm) => {
        // //         if (itm == vm.blinkUserId) {
        // //             vm.lgId = itm;
        // //         }
        // //     });
        // //     setTimeout(function () {
        // //         vm.lgId = 0;
        // //         setTimeout(function () {
        // //             if (vm.chatTab != true) {
        // //                 vm.$refs.ChatsTab.classList.remove("active");
        // //             }
        // //         }, 10);
        // //     }, 6000);

        // // } else if (vm.$refs.ChatsTab.classList.contains('active') == true) {
        // //     vm.blinkUserId = vm.sender.id;
        // //     vm.isUserLoggedInId.forEach((itm) => {
        // //         if (itm != vm.blinkUserId && vm.chatUserInfo.GROUP_ID != data.roomId) {
        // //             var element = document.getElementById(userLoggedIn.GROUP_ID);
        // //             // element.classList.remove('active');
        // //             element.classList.add('blinkingcommon2');
        // //             setTimeout(function () {
        // //                 var element = document.getElementById(userLoggedIn.GROUP_ID);
        // //                 element.classList.remove('blinkingcommon2');
        // //             }, 6000);

        // //         }
        // //     });

        // // } else {
        // //     vm.isUserLoggedInId = 0;
        // //     vm.lgId = 0
        // // }
        // if (vm.userGroupId == data.roomId) {
        //   vm.readMessageUpdate(data.roomId, vm.sender);
        // }
        // //await vm.$socket.emit('get All Chat Persons', vm.sender);

        // let myObj = {
        //             companyId: vm.sender.companyId,
        //             userId: vm.sender.id,
        //             emaiId: vm.sender.emaiId,
        //             name: vm.sender.name,
        //             imgPath: vm.store.getBlobUserUrl + vm.sender.imgPath,
        //             type: vm.type
        //         }
        // vm.$socket.emit('user total', myObj);
        // },
    },
    created: async function () {
        this.ConnectIO()
        this.store.groupList(`emaiId=${this.sender.emaiId}&isGroup=1&room=1`)
    },
    methods: {
        ConnectIO: async function () {

            var vm = this;
            vm.GetContactList();
            vm.chatHistoryDetails = [];
            if (vm.isMobile == true) {
                await vm.$socket.emit('get All Chat Persons', vm.store.GetSenderDetail);
                this.isDataLoaded = false;
            }
            else {
                await vm.$socket.emit('get All Chat Persons', vm.store.GetSenderDetail);
            }

            this.isDataLoaded = false;
        },
        SearchChatUsers: function (evt) {
            debugger
            var vm = this;
            let result = ''
            let result1 = ''
            // if (evt && evt.key != 'Backspace') {
            //     vm.ChatUserListData = [];
            //     var result = vm.tmpChatUserList.filter(x => typeof x.GROUP_NAME === 'string' && x.GROUP_NAME.toLowerCase().includes(vm.txtSearchChatGroups.toLowerCase()));
            //     vm.ChatUserListData = result;
            // }
            // else if (evt && evt.key == 'Backspace') {
            //     vm.ChatUserListData = [];
            //     var result = vm.tmpChatUserList.filter(x => typeof x.GROUP_NAME === 'string' && x.GROUP_NAME.toLowerCase().includes(vm.txtSearchChatGroups.toLowerCase()));
            //     vm.ChatUserListData = result;
            // }
            // else {
            //     vm.ChatUserListData = vm.tmpChatUserList;
            // }
            if (evt && evt.key != 'Backspace') {
                vm.ChatUserListData = [];
                result = vm.tmpChatUserList.filter(x => (Object.keys(x).includes('CONTACTNAME') == '' ? x.GROUP_NAME : x.CONTACTNAME).toLowerCase().includes(vm.txtSearchChatGroups.toLowerCase()));
                vm.ChatUserListData = result;
                // vm.AllContactList = [];
                //  result1 = vm.tmpAllContactUserList.filter(x => (Object.keys(x).includes('CONTACTNAME') == '' ? x.GROUP_NAME : x.CONTACTNAME).toLowerCase().includes(vm.txtSearchChatGroups.toLowerCase()));
                // vm.AllContactList = result1;

            } else if (evt && evt.key == 'Backspace') {
                vm.ChatUserListData = [];
                result = vm.tmpChatUserList.filter(x => (Object.keys(x).includes('CONTACTNAME') == '' ? x.GROUP_NAME : x.CONTACTNAME).toLowerCase().includes(vm.txtSearchChatGroups.toLowerCase()));
                vm.ChatUserListData = result;
                // vm.AllContactList = [];
                //  result1 = vm.tmpAllContactUserList.filter(x => (Object.keys(x).includes('CONTACTNAME') == '' ? x.GROUP_NAME : x.CONTACTNAME).toLowerCase().includes(vm.txtSearchChatGroups.toLowerCase()));
                // vm.AllContactList = result1;

            } else {
                vm.ChatUserListData = vm.tmpChatUserList;
                // vm.ChatUserListData = vm.tmpAllContactUserList;
            }
        },

        GetContactList: async function () {
            let vm = this;
            let allUsers = []
            let type = 'company'
            let url = `search=&pageSize=&pageNum=&sortBy=&sortExp=&type=${type}&city=&departmentId=`;

            await DataService.CompanyContact(url).then(response => {
                if (response.data != null) {
                    allUsers = JSON.parse(response.data);
                    // vm.AllContactList = JSON.parse(response.data);
                    vm.ChatUserListData.forEach(chatUser => {
                        // Find the index of the item in vm.AllContactList by USER_ID
                        let index = allUsers.findIndex(item => item.USER_ID == chatUser.RECEIVER_ID);

                        // If the item exists in vm.AllContactList, remove it
                        if (index !== -1) {
                            allUsers.splice(index, 1);
                        }
                    });

                    // Merge vm.ChatUserListData into vm.AllContactList
                    vm.AllContactList = [...allUsers];
                }
            });
        },
        // get all history of chat user list and history of users
        historyLogUsers(data) {
            debugger
            var vm = this;
            vm.isDataLoaded = false;
            vm.ChatUserListData = [];
            if (data.resultSet.length > 0) {
                vm.chatUsersList = data.resultSet;
                if (vm.isMobile == true && vm.isMobileNewChat == 1) // due to new user add in chat list
                {
                    vm.isMobileNewChat = 0;
                }
                var arr = [];
                data.resultSet.forEach((message) => {
                    if (!arr.includes(message.RECEIVER_ID)) {
                        vm.ChatUserListData.push(message);
                        if (vm.group <= 0) {
                            if (vm.ReceiverId >= 0) { // if tab is already open and video is accepted

                                vm.openRoomChat(vm.group, vm.sender, null, false)
                            }
                            else {
                                vm.group = message.GROUP_ID;
                                vm.chatUserInfo = message;
                                vm.GroupAliasName = message.GROUP_NAME_ALIAS;
                                vm.userGroupId = message.GROUP_ID;
                                vm.groupNameDisplay = message.GROUP_NAME;
                                console.log('vm.group', vm.group)
                                vm.openRoomChat(message.GROUP_ID, vm.sender, null, false, message);

                            }
                        }

                    }
                });
                // Filter records where IS_GROUP is 1 and perform actions with them
                this.groupRecords = vm.ChatUserListData.filter(user => user.IS_GROUP);
            } else {
                vm.groupNameDisplay = '';
                vm.userGroupId = null;
            }
            vm.tmpChatUserList = vm.ChatUserListData;
            this.$socket.emit('onlineUser', this.store.getUserInformation);

        },
        //this mathod inside call from history call logged method for room joinig
        async openRoomChat(roomId, user, lastChatId, isGlobalSearch, itm) {
            let vm = this;

            let name = vm.chatUsersList.find(x => x.GROUP_ID == roomId)
            let RecordNumber = '';
            let param = new Object();
            param = user;
            param.room = roomId;
            param.groupName = name.GROUP_NAME;
            param.lastrow = 0;
            param.recordNumber = RecordNumber;
            param.is_first_time = 1;
            param.lastchatId = lastChatId;
            param.searchForMsg = null;
            param.isGlobalSearch = isGlobalSearch;
            param.alias = name.GROUP_NAME_ALIAS;
            await vm.$socket.emit('joinRoom', param, itm);

            const is_group = vm.GetIsGroup(roomId);
            // if (is_group != "true") {
            //     vm.GetStatus(roomId); // calling for user status
            // } else {
            //     //$('.chat-region').find('.user-info').find('div.status').removeClass();
            // }
            vm.Groupdata = itm;
            await vm.$socket.emit('user chat Read', param);
        },
        //  GetStatus(room) {
        //     let vm = this;
        //     const chatUserId = vm.chatUsersList.find(x => x.GROUP_ID == room)
        //     // var chatUserId = $("#div-left-forChatDiv .contacts-panel #" + room).attr("data-receiver");
        //     this.$socket.emit('get Open Chat User Status', chatUserId);
        // },
        GetIsGroup(room) {
            let vm = this;
            let isgroup = 0;
            isgroup = vm.chatUsersList.find(x => x.GROUP_ID == room)
            return isgroup.IS_GROUP
        },
        selectTab(event) {
            this.activeTabs = event
        },
        //this mathod is used for get chat details on click on chat users/group all chat history details.
        async JoinRooms(itm) {
            debugger
            var vm = this;
            // vm.$refs.chatTab.isDataLoaded = true;
            vm.chatHistoryDetails = [];

            vm.$socket.emit('is typing', {
                user: vm.sender.id,
                Group: itm.GROUP_ID,
                typing: false,
                IsGroup: itm.IS_GROUP
            })
            var paramsObj = {
                alias: itm.GROUP_NAME_ALIAS,
                companyId: vm.sender.companyId,
                emaiId: vm.sender.emaiId,
                groupName: itm.GROUP_NAME,
                id: vm.sender.id,
                imgPath: vm.sender.imgPath,
                is_first_time: 1,
                isGlobalSearch: false,
                lastchatId: null,
                lastrow: 0,
                name: vm.sender.name,
                room: itm.GROUP_NAME_ALIAS
            };
            await vm.$socket.emit('joinRoom', paramsObj, itm);
            itm.isActive == true;
            if (Object.prototype.hasOwnProperty.call(itm, 'GROUP_ID')) {
                var grpName = vm.ChatUserListData.find(x => x.GROUP_ID == itm.GROUP_ID)
                vm.RecieverId = itm.RECEIVER_ID;
                // if(vm.sender.id==itm.RECEIVER_ID)
                // {
                vm.groupNameDisplay = itm.GROUP_NAME ? itm.GROUP_NAME : grpName.GROUP_NAME;
                vm.userGroupId = itm.GROUP_ID;
                // }
                vm.GroupAliasName = itm.GROUP_NAME_ALIAS ? itm.GROUP_NAME_ALIAS : grpName.GROUP_NAME_ALIAS;
                vm.chatUserInfo = itm;
                vm.Groupdata = itm;
            }
            // vm.isDataLoaded=false;
            if (itm.FROM_USER_ID == vm.sender.id) {
                vm.closeGroup()
                await vm.$refs.chatTab.backToChat();
                vm.$refs.chatTab.forwardCancel();
            }
