<template>
<div>
    <div class="modal is-visible" id="video-chat">
        <div class="modal-overlay"></div>
    <div class="modal-wrapper modal-transition uc-video-chat-wrapper">
        <div class="modal-header" style="cursor: move;">
            <h2 class="modal-heading">Video Calling</h2>
        </div>
        <div class="modal-body v-chat-inner">
            <div class="modal-content video-panel">
                <div class="video-call">
                    <div class="child-a">
                        <div id="remotes" style="max-height:450px;overflow:hidden;">
                            <div class="uc_single_videobase p-0">
                              <video ref="remoteVideo" autoplay muted></video>
                            </div>
                        </div>
                        <img src="" alt="Calling..." style="display: none;">
                    </div>
                </div>
                <div style="clear:both;"></div>
                <div class="icons buttons">
                    <a href="javascript:;" id="recording" style="display:none;" class="btn record"></a>
                    <a href="javascript:;" class="loud-speaker unmute"> <i class="fa fa-volume-up"></i></a>
                    <a href="javascript:;" class="modal-toggle call disconnectVideo"> <i class="fa fa-phone"></i></a>
                </div>
            </div>
        </div>
    </div>
    <div class="userc-image user-video-panel rounded">
        <div class="video"><video ref="localVideo" autoplay muted></video></div>
    </div>
</div>
</div>
</template>

  
<script>
export default {
    props: {
        store: {
            type: Object
        },
        callTo:{
          type:Number
        }
    },
    data() {
        return {
            localStream: null,
            remoteStream: null,
            peerConnection: null,
            webSocket: null,
            callStarted: false,
            isCaller: true,
            fromCall:''
        };
    },
    sockets: {
        connect() {
            console.log('Connected to server');
        },
        disconnect() {
            console.log('Disconnected from server');
        },
        recieverVideo: async function (video) {
            console.log("video", video)
            this.fromCall = video.from ? video.from : ''
            if (video.id == this.store.getUserInformation.ID) {

                if (video.type == 'offer') {
                    this.isCaller = false
                    this.initializePeerConnection();
                    this.handleSignalingMessage(video)
                } else if (video.type == 'answer') {
                    this.handleAnswer(video)
                } else if (video.type == 'candidate') {
                    this.handleIceCandidate(video)
                }
            }
        }

    },
    mounted() {
        this.initializeWebSocket();
        this.startCall()
    },
    methods: {
        initializeWebSocket() {
            // Replace 'ws://your_signaling_server_address' with your WebSocket server address

            

        },

        sendIceCandidate(candidate) {
            // Send ICE candidate to remote peer through signaling channel
            console.log("candidate", candidate);
            this.$socket.emit('videoOffer', {
                type: 'candidate',
                candidate: candidate,
                id: this.callTo,from:this.getUserInformation.ID
            })
        },
        startCall() {
            this.callStarted = true; // Set call started flag
            console.log("Call start button is clicked");

            // Initialize peer connection
            this.initializePeerConnection();
        },
        receiveCall() {
            // Enable the flag to indicate that this client is the receiver
            this.isCaller = false;

            // If the peer connection is not already initialized, initialize it
            if (!this.peerConnection) {
                console.log("Receive call method start");
                this.initializePeerConnection();
            }
        },
        initializePeerConnection() {
            // Create peer connection configuration
            const configuration = {
                iceServers: [{
                    "url": "stun:stun2.1.google.com:19302"
                }]
            };

            // Create peer connection
            this.peerConnection = new RTCPeerConnection(configuration);
            console.log("peerConnection", this.peerConnection);
            // Add local stream to peer connection
            navigator.mediaDevices.getUserMedia({
                    video: true,
                    audio: true
                })
                .then(stream => {
                    this.$refs.localVideo.srcObject = stream;
                    this.localStream = stream;

                    stream.getTracks().forEach(track => {
                        this.peerConnection.addTrack(track, stream);
                    });
                })
                .catch(error => {
                    console.error('Error accessing media devices:', error);
                });

            // Event listener for incoming remote streams
            this.peerConnection.ontrack = event => {
                if (!this.remoteStream) {
                    this.remoteStream = new MediaStream();
                    console.log("this is done", this.remoteStream);
                    this.$refs.remoteVideo.srcObject = this.remoteStream;
                }
                this.remoteStream.addTrack(event.track, this.remoteStream);
            };

            // Event listener for ICE candidates
            this.peerConnection.onicecandidate = event => {
                if (event.candidate) {
                    // Send ICE candidate to remote peer through signaling channel
                    console.log("event.candidate", event.candidate);
                    this.sendIceCandidate(event.candidate);
                }
            };

            // If this client is the caller, create and send offer
            if (this.isCaller) {
                this.peerConnection.createOffer()
                    .then(offer => {
                        return this.peerConnection.setLocalDescription(offer);
                    })
                    .then(() => {
                        // Send offer to remote peer through signaling channel
                        this.$socket.emit('videoOffer', {
                            type: 'offer',
                            offer: this.peerConnection.localDescription,
                            id: this.callTo,from:this.store.getUserInformation.ID
                        })
                    })
                    .catch(error => {
                        console.error('Error creating offer:', error);
                    });
            }
        },
        handleSignalingMessage(message) {
            console.log("message", message)
            // Handle signaling message received from the remote peer
            switch (message.type) {

                case 'offer':
                    // Handle incoming offer
                    console.log("offer", message.offer);
                    this.handleOffer(message.offer);
                    break;
                case 'answer':
                    // Handle incoming answer
                    console.log("answer", message.answer);
                    this.handleAnswer(message.answer);
                    break;
                case 'candidate':
                    // Handle ICE candidate message
                    console.log("candidate", message.candidate);
                    this.handleIceCandidate(message.candidate);
                    break;
                default:
                    console.warn('Received unknown message type:', message.type);
            }
        },
        async handleOffer(offer) {
            console.log("In handleOffer method", this.peerConnection);
            // Set remote description of the offer
            await this.peerConnection.setRemoteDescription(new RTCSessionDescription(offer));

            // Create an SDP answer
            const sdpAnswer = await this.peerConnection.createAnswer();
            // Set the local description with the SDP answer
            await this.peerConnection.setLocalDescription(sdpAnswer);
            this.$socket.emit('videoOffer', {
                type: 'answer',
                answer: this.peerConnection.localDescription,
                id:this.fromCall,from:''
            })

        },
        handleAnswer(answer) {
            console.log('answer', answer, this.peerConnection);
            // Set remote description of the answer
            this.peerConnection.setRemoteDescription(new RTCSessionDescription(answer))
                .then(() => {
                    console.log('Remote description set successfully.');
                })
                .catch(error => {
                    console.error('Error setting remote description:', error);
                });
        },
        handleIceCandidate(candidate) {
            console.error('Error setting remote description:', candidate);
            // Add ICE candidate to peer connection
            this.peerConnection.addIceCandidate(new RTCIceCandidate(candidate))
                .then(() => {
                    console.log('ICE candidate added successfully.');
                })
                .catch(error => {
                    console.error('Error adding ICE candidate:', error);
                });
        }
    }
};
</script>

  
<style scoped>
video {
    max-width: 100%;
    height: auto;
    margin-bottom: 10px;
}
</style>
