<template>
<div class="div_forDifferentTabs content" id="v-pills-contacts1" style="display: block;">
    <div id="contactPanel">
        <div class="chat-header">
            <div class="name">
                {{ contactDetailShow ? userInformation.CONTACTNAME : '' }}
            </div>
            <div class="chat-links">
                <a id="pills-contacts-tab" title="" class="phone nav-link active" data-toggle-tooltip="tooltip" data-original-title="Contacts" @click="getContactRecordBy('Contacts')"><i class="fa fa-address-book"></i></a>
                <a id="pills-favorites-tab" title="" class="video_icon nav-link" data-toggle-tooltip="tooltip" data-original-title=".Favorite Contact" @click="getContactRecordBy('Favorite')"><i class="fa fa-star-o"></i></a>
                <a id="pills-client-tab" title="" class="file nav-link" data-toggle-tooltip="tooltip" data-original-title="Clients"><i class="fa fa-user-o " @click="getContactRecordBy('client')"></i></a>
                <!-- <a id="pills-usergroups-tab" title="" class="users nav-link" data-toggle-tooltip="tooltip" data-original-title="Groups"><i class="fa fa-users" @click="getContactRecordBy('Groups')"></i></a> -->
            </div>
        </div>

        <div class="msgbx_left position-relative" v-if="contactDetailShow">
            <!-- div for message chat history details -->
            <div class="scroll-wrapper scrollbar-inner scroll-content">
                <div class="scrollbar-inner scroll-content" v-chat-scroll id="div_chatDetails" style="height: auto;margin-bottom: 0px;    margin-right: 0px;    max-height: calc(100vh - 390px) !important;">
                    <div id="chat-window" class="chatpanesbasemid">
                        <div id="message-list" class="messageslists_uc60">
                            <div v-for="groupDate,index in groupBydates(messageList)" :key="index">

                                <div class="dividerdate mb-4 mt-4">
                                    <div class="dividerdate_box">{{ getPreviousDay(groupDate.date) }}</div>
                                </div>
                                <div class="clearfix"></div>

                                <template v-for="item,index in groupDate.data">

                                    <div :key="index" class="conv_base  mb-4" :class="{ 'float-right': item.from == fromNumber, 'float-left': item.from != fromNumber, 'undelivered': item.status == 'undelivered', 'delivered': item.status == 'delivered', 'failure': item.status == 'failed', 'blinkingcommon': item.blinking == true }">
                                        <div class="conv_base_name float-left w-100">{{ item.from == fromNumber ?
            store.getUserInformation.Name : userInformation.CONTACTNAME }}</div>

                                        <div class="conv_base_desc float-left w-100 mt-2">{{ item.body ? item.body :
            '' }}</div>
                                        <div class="conv_base_time float-left w-100 mt-2">
                                            <div class="conv_time float-left">{{ getTime(item.dateCreated) }}</div>
                                            <div class="conv_date float-right">{{ item.dateCreated ?
            getDateOnly(item.dateCreated) : getDateInFormat(item.detSet) }}
                                            </div>
                                        </div>
                                        <div :class="{ 'cb_undelivered': item.status == 'undelivered', 'cb_delivered': item.status == 'delivered', 'cb_failure': item.status == 'failed' }" class="float-left w-100 mt-2 text-right text-capitalize" v-if="item.from == fromNumber"><i class="fa fa-exclamation-triangle  mr-2 font-13" v-if="item.status == 'undelivered' || item.status == 'failed'"></i>{{
            item.status }}</div>
                                    </div>
                                    <div class="clearfix" :key="index"></div>

                                </template>
                            </div>

                        </div>

                    </div>
                </div>
            </div>

            <div class="col-12 p-0 float-left message-footer2">
                <div class="conv_box float-left w-100">
                    <div class="conv_box_tab p-3 w-100 float-left">
                        <a href="javascript:;">SMS</a>
                    </div>
                    <div class="conv_box_desc p-2 w-100 float-left">
                        <textarea v-model="message"></textarea>
                    </div>
                    <div class="conv_box_btns p-2 w-100 float-left">
                        <div class="col-6 float-left"></div>
                        <div class="col-6 float-right text-right">
                            <a href="javascript:;" class="btn btn-light text-secondary" id="btnSaveTicket" @click="clear()"><i class="fa fa-trash pr-2 text-secondary"></i>Clear</a>
                            <a @click="messageSend('twilio')" class="btn btn-info text-white" id="btnSaveTicket"><i class="fa fa-paper-plane pr-2 text-white"></i>{{ $t('Send') }}</a>
                        </div>
                    </div>

                </div>

            </div>
        </div>

        <div id="contacts" v-else>
            <div class="contact_byname">
                <div class="contacts-alpha px-2" id="div_contactLetterTab" style="display: block;">
                    <a href="javascript:;" class="searchContactAlpha" @click="FilterFirstName = 'all'" v-if="store.getContactList.length > 0">{{ $t('All') }}</a>
                    <a href="javascript:;" class="searchContactAlpha" v-for="name in store.getHearderContactNameFilter" :key="name" :class="{ 'active': FilterFirstName == name }" @click="FilterFirstName = name">{{ name
                            }}</a>
                </div>

            </div>
            <div class="dev_contact-internal-tabs active show" id="pills-contacts" style="display: block;">
                <div class="scroll-wrapper scrollbar-inner scroll-content scroll-scrolly_visible">
                    <div id="contact-window" class="scrollbar-inner scroll-content scroll-scrolly_visible" :style="{ 'height': 'auto', 'margin-bottom': 0 + 'px', 'margin-right': 0 + 'px', 'max-heigh': pageHeightScroll + 'px' }">
                        <div class="recent-contacts">
                            <div id="contact-list" class="chatpanesbasemid p-0 pt-2">
                                <div class="recent-contacts mt-0 ">
                                    <div class="recent-contacts heading px-2 mt-0 py-2 " v-if="topFilterSelect == 'Favorite'">
                                        <span id="contactListHeader">Favorites (All)</span>
                                    </div>
                                    <div class="recent-contacts-listing-wrapper call_list" data-simplebar="">

                                        <div class="member card-contact recent-contacts-listing" v-for="contact,index in filter(FilterFirstName)" :key="index">
                                            <div class="member-img">
                                                <img :src="contact.USER_IMAGE ? contact.USER_IMAGE : 'https://talygenv.github.io/Vue-UI-Content/images/UC/uc_user_new.jpg'">
                                                <!-- <i class="fa fa-user"></i> -->
                                            </div>
                                            <div class="member-detail">
                                                <p @click="contactDetail(contact)" class="text-truncate" :title="contact.CONTACTNAME"><a href="javascript:;" id="ancEditTwilio" class="text-primary font-weight-medium">{{
            contact.CONTACTNAME }}</a>
                                                </p>
                                                <p class="sm">
                                                    {{ contact.DEPARTMENT_NAME ? contact.DEPARTMENT_NAME : '-' }}
                                                </p>
                                                <div class="log uc_fav pr-2 icons">
                                                    <a class="phone" href="javascript:;" @click="callTouser(contact.USER_PHONE)"><i class="fa fa-phone"></i></a>
                                                    <a class="smsNexmo sms" data-phonenumber="" href="javascript:;" @click="smsToContact(contact)" title="Message"><i class="fa fa-envelope"></i></a>
                                                    <a class="chatCreate chat" v-on:click="NewChat(contact)" title="Chat" href="javascript:;"><i class="fa fa-comments"></i></a>
                                                    <a class="videoCreate video" href="javascript:;" title="Video Call" @click="videoCreate(contact)"><i class="fa fa-video-camera"></i></a>
                                                    <a class="favrt star" href="javascript:;" title="Convert To Favorite Contact" @click="addFavourite(contact)"><i :class="contact.FAVOURITE_ID == null ? 'fa fa-star-o' : 'fa fa-star'"></i></a>

                                                </div>
                                            </div>
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="scroll-element scroll-x scroll-scrolly_visible">
                        <div class="scroll-element_outer">
                            <div class="scroll-element_size"></div>
                            <div class="scroll-element_track"></div>
                            <div class="scroll-bar" style="width: 88px;"></div>
                        </div>
                    </div>
                    <div class="scroll-element scroll-y scroll-scrolly_visible">
                        <div class="scroll-element_outer">
                            <div class="scroll-element_size"></div>
                            <div class="scroll-element_track"></div>
                            <div class="scroll-bar" :style="{ 'overflow': 'auto', 'height': pageHeight + 'px', 'top': pageHeightScroll + 'px' }">
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="dev_contact-internal-tabs" id="pills-usergroups" v-if="filter(FilterFirstName).length == 0">
                <div id="accordion-window" class="chatpanesbasemid">
                    <div class="recent-contacts mt-0">
                        <div class="recent-contacts-listing-wrapper min-height65">
                            <div id="accordion" class="grouplistingbase"></div>
                        </div>
                    </div>
                </div>
                <div id="accordion-window-error" class="text-center text-danger border p-2 no-record" style="">
                    {{ $t('NoRecordfound') }}
                </div>
            </div>
        </div>
    </div>

    <div id="sms-popup" class="files-attachement-wrapper modal is-visible" v-if="smsPopupShow">
        <div class="modal-overlay modal-toggle"></div>
        <div class="modal-wrapper modal-transition send-sms">
            <div class="modal-header">
                <button id="sms-cancel" class="modal-close modal-toggle" @click="smsPopupShow=false;toNumber='';"><i class="fa fa-close"></i></button>
                <h2 class="modal-heading">Send SMS</h2>
            </div>
            <div class="modal-body sms-content">
                <div class="form-group" style="width: 100%;margin-bottom: 0px;padding: 10px 10px;">
                    <textarea id="smscontent" maxlength="160" class="form-control" placeholder="Type your message here..." style="height: 200px;" v-model="message"></textarea>
                    <span style="font-size: 12px;" class="text-danger" id="rchars">154</span> <span style="font-size: 12px;" class="text-danger"> Character(s) Remaining</span>
                </div>
            </div>
            <div class="modal-footer bottom-btn" style="margin: 10px;">
                <select id="smsSelect" class="btn btn-primary btn-select text-white btn-success  m-custom-select fa" v-model="sendMessageVia" title="Send SMS" @change="messageSend()">
                    Send href="javascript:;"&gt;
                    <option class="bg-secondary fa fa-input" value="0">  &nbsp;Send</option>
                    <option class="bg-secondary" value="twilio">Send by Twilio</option>
                    <option class="bg-secondary" value="nexo">Send by Nexmo</option>
                </select>
            </div>
        </div>
    </div>

    <!-- <WebRTCComponent v-if="videoCallShow" v-bind:store="store" :callTo="callToUserInfo.USER_ID" v-on:closeVideo="closeVideo"></WebRTCComponent> -->
</div>
</template>

<script>
import DataService from '../services/DataService';
import {
    getDateOnly,
    getDateInFormat,
    getPreviousDay
} from "../assets/common/vue-common"
export default {
    components: {

    },
    name: "Contacts",
    props: {
        store: {
            type: Object
        },

        sideBarFilter: {
            type: Object
        }
    },
    data() {
        return {
            messageList: [],
            Grouptitle: '',
            sender: {},
            selectedMembers: [],
            FilterFirstName: 'all',
            topFilterSelect: 'Contacts',
            MemberList: [],
            isContact: true,
            FAVOURITE_ID: null,
            pageHeightScroll: window.innerHeight,
            pageHeight: window.innerHeight - 115,
            groupInformation: [],
            groupMembers: [],
            activeGroup: '',
            blobUrl: '',
            containerName: '',
            imgPath: '',
            blobUserUrl: '',
            noImageUrl: 'https://talygenv.github.io/Vue-UI-Content/images/DefaultImages/uc_user_new.jpg',
            usertype: '',
            groupId: '',
            smsPopupShow: false,
            fromNumber: this.store.getTwilioAccountDetails?.NUMBER,
            toNumber: "",
            message: '',
            sendMessageVia: 0,
            contactDetailShow: false,
            userInformation: '',

        }
    },
    created: async function () {

        var vm = this;
        vm.usertype = vm.GetUserInfo ? vm.GetUserInfo.UserType : '';
        vm.containerName = vm.GetUserInfo ? vm.GetUserInfo.ContainerName : '';
        vm.imgPath = vm.GetUserInfo ? vm.GetUserInfo.Avatar : '';
        vm.GetBlobUrl();
        //await vm.GetContactList();
        //vm.selectedMembers.push(vm.GetUserInfo.id);

    },

    mounted() {

        //console.log("checkk",this.store.getContactList)
        window.addEventListener("resize", this.updateDimensions);

    },
    beforeDestroy() {
        window.removeEventListener("resize", this.updateDimensions);
    },
    computed: {
        updateDimensions() {
            debugger
            return () => {
                this.pageHeightScroll = window.innerHeight;
                this.pageHeight = window.innerHeight;
            }
        },
    },
    sockets: {
        connect() {
            console.log('Connected to server');
        },
        disconnect() {
            console.log('Disconnected from server');
        },
        messageGet: function (details) {
            //this.toNumber = "+16506660036"
            // this.toNumber = "+16506660036"
            //console.log("details", details)
            if (this.toNumber == details.From) {
                details.body = details.Body
                details.from = details.From
                details.to = details.To
                details.status = details.SmsStatus
                details.dateCreated = new Date()
                details.blinking = true
                details.dateCreated = details.dateCreated.toString()
                this.messageList.push(details)
            }
            ///console.log("details", details)
        },
        messageStatus: function (details) {
            //this.toNumber = "+16506660036"
            if (this.toNumber == details.To) {
                var foundIndex = this.messageList.findIndex(item => item.sid == details.SmsSid)
                this.messageList[foundIndex] ? this.messageList[foundIndex].status = details.SmsStatus : ''
            }
        },

    },
    methods: {
        GetBlobUrl: async function () {
            await DataService.GetBlobUrl().then(response => {
                this.blobUserUrl = response.data[0].CONFIG_DATA_VALUE + '/' + this.containerName + '/' + 'Upload' + '/' + 'UserProfileImage' + '/'
            })
        },

        callTouser: async function (number) {
            var vm = this;
            if (number) {
                if (vm.store.$state.callToken) {
                    vm.$parent.callFromOtherTab(number)
                } else {
                    vm.ShowAlert(vm.$t("ConnectDevice"), "failure", true, vm.$t("Alert"));
                }

            } else {
                vm.ShowAlert(vm.$t('MobileNotAvailable'), "failure", vm.$t('Alert'));
            }
        },
        close: function () {
            debugger
        },

        filter(letter) {
            this.FilterFirstName = letter;
            const contactList = this.store.getContactList;

            if (this.topFilterSelect === 'Favorite') {
                return this.filterContacts(contactList, letter, true);
            } else {
                return letter !== 'all' ? this.filterContacts(contactList, letter, false) : contactList;
            }
        },

        filterContacts(contactList, letter, isFavorite) {
            return contactList.filter(contact => {
                const isMatchingLetter = this.isMatchingLetter(contact, letter);
                const isFavoriteContact = isFavorite ? contact.FAVOURITE_ID != null : true;
                return isMatchingLetter && isFavoriteContact;
            });
        },

        isMatchingLetter(contact, letter) {
            if (letter === 'all') return true;
            if (!contact.CONTACTNAME) return false;
            return contact.CONTACTNAME.substring(0, 1).toUpperCase() === letter;
        },
        async getContactRecordBy(event) {
    this.topFilterSelect = event;
    this.contactDetailShow = false;
    this.FilterFirstName = 'all';

    let departmentId = typeof this.sideBarFilter.id === 'number' ? this.sideBarFilter.id : '';
    let city = typeof this.sideBarFilter.id !== 'number' ? this.sideBarFilter.id : '';
    let url = this.constructUrl(event, city, departmentId);
    await this.store.getContact(url);
},
constructUrl(event, city, departmentId) {
    if (event === 'Contacts') {
        return `search=&pageSize=&pageNum=&sortBy=&sortExp=&type=&city=${city}&departmentId=`;
    }
    if (event === 'Favorite') {
        return `search=&pageSize=&pageNum=&sortBy=&sortExp=&type=&city=${city}&departmentId=${departmentId}`;
    }
    return `search=&pageSize=&pageNum=&sortBy=&sortExp=&type=${event}&city=${city}&departmentId=${departmentId}`;
},
        addFavourite(itm) {
            if (itm.FAVOURITE_ID) {
                itm.FAVOURITE_ID = null
            } else {
                itm.FAVOURITE_ID = 78787
            }
            console.log("item", itm.FAVOURITE_ID)
            //  DataService.fevupdateRoom.then(()=>{

            // })

        },

        smsToContact(itm) {
            this.userInformation = itm
            if (itm.USER_PHONE) {
                this.toNumber = itm.USER_PHONE
                this.smsPopupShow = true
            } else {
                this.ShowAlert(this.$t('MobileNotAvailable'), "failure", this.$t('Alert'));
            }
        },
        contactDetail(itm) {
            var vm = this;
            if (itm.USER_PHONE) {
                vm.userInformation = itm
                vm.toNumber = itm.USER_PHONE
                vm.contactDetailShow = true
                vm.$socket.emit('smsList', {
                    from: vm.fromNumber, //this.fromNumber
                    to: vm.toNumber
                }, function (record) {
                    vm.messageList = record
                })
            } else {
                this.ShowAlert(this.$t('MobileNotAvailable'), "failure", this.$t('Alert'));
            }
        },
        groupBydates(messageList) {
            const groups = messageList.reduce((groups, data) => {
                const date = new Date(new Date(data.dateCreated).toDateString());
                if (!groups[date]) {
                    groups[date] = [];
                }
                groups[date].push(data);
                return groups;
            }, {});

            // Edit: to add it in the array format instead
            return Object.keys(groups).map((date) => {
                return {
                    date,
                    data: groups[date]
                };
            });
        },
        messageSend(event) {
            var vm = this;
            if (vm.message != '') {
                console.log("testt", vm.message)
                if (vm.sendMessageVia == 'twilio' || event == 'twilio') {
                    vm.$socket.emit('twilioSendMessage', {
                        from: '+12513095602', //this.fromNumber
                        to: '+16506660036', //this.toNumber
                        body: vm.message,
                    }, function (record) {
                        vm.message = ''
                        console.log("createApiKey", record)
                        vm.messageList.push(record)

                    })
                }
            } else {
                this.ShowAlert(this.$t('PleaseEnterMessage'), "failure", this.$t('Alert'));
            }
        },
        //     validateEmailAddress: function(event) {
        //         console.log("testt",event.key)
        //         if (event.shiftKey!==true && event.key === "Enter") {
        //     messageSend('twilio')
        //   }    

        //},
        clear() {
            this.message = ''
        },
        getTime(date) {
            date = new Date(date)
            const newInputDate = date;

            const timeStamp = new Date(newInputDate);
            return timeStamp.toLocaleTimeString(navigator.language, {
                hour: '2-digit',
                minute: '2-digit'
            })
        },
        NewChat(item) {
            console.log("item", item)
            this.$parent.OpenTicketReceiver(item)
            //this.$emit('')
        },

        videoCreate(contect) {
            let obj = {
                USER_ID: contect.USER_ID,
                CONTACTNAME: contect.CONTACTNAME,
                USER_IMAGE: contect.USER_IMAGE

            }
            this.$parent.videoCreate(obj)
        },
    },

    setup() {
        return {
            getDateOnly,
            getDateInFormat,
            getPreviousDay

        }
    }
}
</script>

<style>
.message-footer2 {
    position: relative;
    margin: 0 auto;
    border-radius: 3px;
    border: 1px solid #dadada;
    background: #ffffff;
    display: flex;
    flex-direction: column;
}
</style>
