                    }
                )
            }
            // var param = new Object(); // update receiver end with message read
            // param = message;
            await vm.$emit('readMessageUpdate', message.roomId, vm.sender);
            vm.scrollBottom()

        },
        newMessage: function (message) {

            var vm = this;
            var currentdate = new Date()
            if (message.chatMessageToken == '' || message.chatMessageToken == undefined) {
                if (message.parentId) {
                    vm.chatHistoryDetails.resultSet.push(
                        {
                            CHATLOG_ID: message.chatId,
                            MESSAGE: message.text,
                            GROUP_ID: message.room,
                            FROM_USER_ID: vm.store2.GetUserInfo.ID,
                            CREATED_AT: currentdate,
                            MESSAGE_TOKEN: message.msg_token,
                            AVATAR: message.sImg,
                            IS_READ: 0,
                            USER_NAME: message.from,
                            PARENT_MESSAGE: message.parent_msg,
                            CHAT_LOG_REF_ID: message.parentId,
                            PARENT_DETAIL: message.msg_detail,
                            reactedUser: null
                        }
                    )
                }
                else {
                    vm.chatHistoryDetails.resultSet.push(
                        {
                            CHATLOG_ID: message.chatId,
                            MESSAGE: message.text,
                            GROUP_ID: message.room,
                            FROM_USER_ID: vm.store2.GetUserInfo.ID,
                            CREATED_AT: currentdate,
                            MESSAGE_TOKEN: message.msg_token,
                            AVATAR: message.sImg,
                            IS_READ: 0,
                            USER_NAME: message.from,
                            reactedUser: null,
                        }
                    )
                }
            }
            else {
                var findChatMessageIndex = vm.chatHistoryDetails.resultSet.findIndex(x => x.MESSAGE_TOKEN == message.chatMessageToken)

                if (findChatMessageIndex !== -1) {
                    // Use Vue.set to ensure reactivity
                    vm.$set(vm.chatHistoryDetails.resultSet[findChatMessageIndex], 'MESSAGE', message.text);
                    vm.$set(vm.chatHistoryDetails.resultSet[findChatMessageIndex], 'CREATED_AT', currentdate);
                }
            }
            vm.isDataLoaded = false;
            vm.scrollBottom();
        },
