                vm.$socket.emit('createMessage', { text: gifSrc, room: roomId, parentId: parent_id, parentTxt: parent_text, parentDtl: detail, chatMessageToken: vm.chatMessageToken }, function () {
                });
                // await vm.$emit('ConnectIO');
