        GetGifImages: async function (txt) {

            var vm = this;
            vm.GifImageData = [];
            vm.isDataGifLoaded = true;
            var search_term = txt == undefined ? '' : txt;
            var apikey = "YP55GXTIJ99N";
            var lmt = 8;
            await axios.get('https://api.tenor.com/v1/anonid?key=' + apikey,
                {
                    headerrs: {
                        'Content-Type': 'multipart/form-data',
                    }
                }).then(function (response) {
                    var url = `tag=${search_term}&key=${apikey}&limit=${lmt}&anon_id=${response.data.anon_id}`;
                    axios.get('https://api.tenor.com/v1/search?' + url,
                        {
                            headers: {
                                'Content-Type': 'multipart/form-data',
                            }
                        }).then(function (response) {
                            vm.GifImageData = response.data.results;
                            vm.isDataGifLoaded = false;
                        })
                })
        },
