        async updateGroup() {
            var vm = this;
            vm.groupShow = true
            let obj = {
                companyId: vm.GetUserInfo.CompanyId,
                id: vm.GetUserInfo.ID,
                emaiId: vm.GetUserInfo.Email,
                name: vm.GetUserInfo.Name,
                imgPath: vm.store.getBlobUserUrl + vm.GetUserInfo.Avatar,
                type: vm.type
            }
            var roomdata = vm.selectedMembers.map(obj => obj.USER_ID).join(',')
            var params = new Object();
            params = obj;
            params.userIds = roomdata
            params.groupName = vm.Grouptitle
            let isGroupOrNot = (params.userIds.split("_").length - 1);
            if (isGroupOrNot > 1) {
                isGroupOrNot = 1;
            } else {
                isGroupOrNot = 0;
            }
            params.groupId = vm.groupId
            params.fromSP = 0
            params.GROUP_ID_OUT = 0
            params.isGroupOrNot = isGroupOrNot
            await vm.store.addOrUpdateGroup(params)
            if (vm.store.addUpdateGroupResState[0]) {
                vm.topFilterSelect = 'Groups'
                vm.updategroup = false
                vm.$parent.closeGroup()
                vm.Grouptitle = ''
                vm.selectedMembers = []
                vm.selectedMembers.push({
                    "USER_ID": vm.GetUserInfo.ID,
                    "CONTACTNAME": vm.GetUserInfo.Name
                })
                vm.groupId = ''
                vm.store.groupList(`emaiId=${this.GetUserInfo.Email}&isGroup=1&room=1`)
            }

