                            <p>
                                <span id="span_callerNameAndNumber" style="font-weight: bold;font-size: 22px;color: #ffa751;"></span>
                                <span id="span_callTimeAfterEnd" style="font-size: 15px;color: #ff7e00;"></span>

                            </p>
                            <div class="btn-rw">
                                <button type="submit" id="btn_SaveDetails" class="btn btn-success btn-sm rounded mr-2"><i class="fa fa-save pr-2"></i><span>Save</span></button>
                                <button id="btn_cancelCallDetails" class="btn btn-danger btn-sm rounded" @click="cancelCallComment"><i class="fa fa-times pr-2"></i><span>Cancel</span></button>

                            </div>
                        </div>
                    </form>
                </ValidationObserver>
            </div>
            <div class="chat-content tab-detail">
                <ul class="tabs" id="pills-tab" role="tablist">
                    <li class="nav-link " :class="activeTab=='Ticket' ? 'current' : '' " @click="tabSelect('Ticket')">
                        <a data-toggle="pill" role="tab">Ticketing</a>
                    </li>
                    <li class="nav-link" :class="activeTab=='crm' ?'current' : '' " @click="tabSelect('crm')">
                        <a data-toggle="pill" role="tab">CRM</a>
                    </li>
                    <li class="nav-link" :class="activeTab=='project' ? 'current' : '' " @click="tabSelect('project')">
                        <a role="tab">Project Management</a>
                    </li>
                </ul>
                <div class="uc_tabcontent" :class="'current' " id="pills-tabContent" v-if="activeTab=='Ticket' || activeTab=='project'">
                    <div class="tab-content current" id="pills-ticketing" role="tabpanel" aria-labelledby="pills-ticketing-tab">
                        <div id="container" class="chatpanesbase chatpane_100 pt-0">
                            <div id="chat-window" class="chatpanesbasemid border">
                                <div id="message-list" class="" style="overflow-x:hidden;height:51vh" data-simplebar="">
                                    <div class="col-12 float-left p-0">
                                        <div class="table-responsive">
                                            <table class="table table-bordered " id="table_for_Call_Ticketing">
                                                <thead class="thead-light">
                                                    <tr>
                                                        <th v-for="header in CRMcolumn[0][activeTab]" :key="header.name">{{ header.name }}</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr v-for="ticket,index in store.getCallTabsInformation" :key="index">
                                                        <td v-for="header in CRMcolumn[0][activeTab]" :key="header.name">
                                                            <div v-if="header.name=='Action'">
                                                                <a @click="viewRecords(ticket[header.dataBaseName])">View </a>
                                                            </div>
                                                            <div v-else-if="header.dataBaseName=='PLANNED_START_DATE' || header.dataBaseName=='PLANNED_END_DATE' || header.dataBaseName=='CREATED_AT'">
                                                                {{ ticket[header.dataBaseName] ?  getDateInFormat(ticket[header.dataBaseName]) :'' }}
                                                            </div>
                                                            <div v-else> {{ ticket[header.dataBaseName] }}</div>
                                                        </td>
                                                    </tr>
                                                    <tr v-if="store.getCallTabsInformation.length == 0">
                                                        <td colspan="10" class="text-center text-danger no-record">{{ $t('NoRecordfound')}}</td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
                <div v-if="activeTab=='crm'" :class="activeTab=='crm' ?'current' : '' " class="tab-content current" id="pills-crm" role="tabpanel" aria-labelledby="pills-crm">
                    <div class="crm-detail">
                        <div class="menu_left">
                            <ul id="pills-tab" role="tablist">
                                <li :class="crmTabSelect=='Leads' ? 'current':''">
                                    <a id="pills-leads-tab" data-toggle="pill" role="tab" aria-controls="pills-leads" aria-selected="true" @click="getOrderDetails('Leads')"><i class="fa fa-users"></i><span> {{ $t('Leads') }}</span></a>
                                </li>
                                <li :class="crmTabSelect=='CRM_DEALS' ? 'current':''">
                                    <a id="pills-deals-tab" data-toggle="pill" role="tab" aria-controls="pills-deals" aria-selected="false" @click="getOrderDetails('CRM_DEALS')"><i class="fa fa-file-text"></i><span> {{ $t('CRM_DEALS') }}</span></a>
                                </li>
                                <li :class="crmTabSelect=='Quote' ? 'current':''">
                                    <a id="pills-quotes-tab" data-toggle="pill" role="tab" aria-controls="pills-quotes" aria-selected="false" @click="getOrderDetails('Quote')"><i class="fa fa-list"></i><span>{{ $t('Quote') }} </span></a>
                                </li>
                                <li :class="crmTabSelect=='PurchaseOrderWithoutRef' ? 'current':''">
                                    <a id="pills-purchaseOrder-tab" data-toggle="pill" role="tab" aria-controls="pills-purchaseOrder" aria-selected="false" @click="getOrderDetails('PurchaseOrderWithoutRef')"><i class="fa fa-bar-chart"></i><span>{{ $t('PurchaseOrderWithoutRef') }} </span></a>
                                </li>
                                <li :class="crmTabSelect=='CRM_SALES_ORDERS' ? 'current':''">
                                    <a id="pills-Sales-Order-tab" data-toggle="pill" role="tab" aria-controls="pills-salesOrders" aria-selected="true" @click="getOrderDetails('CRM_SALES_ORDERS')"><i class="fa fa-line-chart"></i><span>{{ $t('CRM_SALES_ORDERS') }}</span></a>
                                </li>
                                <li :class="crmTabSelect=='Invoices' ? 'current':''">
                                    <a id="pills-Invoice-tab" data-toggle="pill" role="tab" aria-controls="pills-invoice" aria-selected="true" @click="getOrderDetails('Invoices')"><i class="fa fa-file"></i><span>{{ $t('Invoices') }} </span></a>
                                </li>
                            </ul>
                        </div>
                        <div class="detail_right uc_tabcontent" id="pills-tabContent">
                            <div class="internal-tab-content current" id="pills-leads" role="tabpanel" aria-labelledby="pills-leads-tab">
                                <div class="detail_heading">
                                    <h3>{{ $t(crmTabSelect) }}</h3>
                                    <!-- <span class="add"><a href="javascript:;" id="btn_addLead" onclick="loadFormForAddLeadOrContact('CRM_LEADS')" style="color: #fff;"><i class="fa fa-plus"></i> Add a Lead</a></span> -->
                                </div>
                                <div id="container" class="chatpanesbase chatpane_100 pt-0">
                                    <div class="recent-contacts mt-0">
                                        <div class="col-12 float-left p-0">
                                            <div class="table-responsive">
                                                <table class="table table-bordered " id="table_for_Call_LeadDetails">
                                                    <thead class="thead-light">
                                                        <tr>

                                                            <th v-for="header in CRMcolumn[0][crmTabSelect]" :key="header.name">{{ header.name }}</th>

                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        <tr v-for="record,index in store.getCrmDetailRecord" :key="index">
                                                            <td v-for="header in CRMcolumn[0][crmTabSelect]" :key="header.name">
                                                                <div v-if="header.name=='Action'">
                                                                    <a @click="viewRecords(record[header.dataBaseName])">View </a>
                                                                </div>
                                                                <div v-if="header.name!='Action'">
                                                                    {{ record[header.dataBaseName] }}
                                                                </div>
                                                            </td>
                                                        </tr>
                                                        <tr v-if="store.getCrmDetailRecord.length == 0">
                                                            <td colspan="10" class="text-center text-danger no-record">{{ $t('NoRecordfound')}}</td>
                                                        </tr>
                                                    </tbody>
                                                </table>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>

        </div>
    </div>
    <div class="modal popup-call is-visible" id="modal_MakeCall" v-if="makeCallPopupShow">
        <div class="modal-overlay modal-toggle"></div>
        <div class="modal-wrapper modal-transition">
            <div class="modal-header">
                <button class="modal-close modal-toggle" @click="makeCallPopupShow=false"><i class="fa fa-close"></i></button>
                <h2 class="modal-heading">Call By</h2>
            </div>
            <div class="modal-body">
                <a class="call-item" href="javascript:;" id="btn_makeNexmoCall" @click="calling('nexo')"> <img src="https://talygenv.github.io/Vue-UI-Content/images/UC/ringcentral-logo.png"></a>
                <a class="call-item" href="javascript:;" id="btn_makeCall" @click="calling('asterisk')"><img src="https://talygenv.github.io/Vue-UI-Content/images/UC/ast_logo.png"></a>
                <a class="call-item" href="javascript:;" id="btn_makeVonageCall" @click="calling('vonage')"><img src="https://talygenv.github.io/Vue-UI-Content/images/UC/vonage.png"></a>
                <a class="call-item" href="javascript:;" id="btn_makeVonageCall" @click="calling('twilio')"><img src="https://talygenv.github.io/Vue-UI-Content/images/UC/twilio-logo-red.png"></a>
                <!-- <a class="call-item" href="javascript:;" id="btn_makeVonageCall" @click="calling('videoCall')">Video</a> -->
            </div>
        </div>
    </div>
    <WebRTCComponent v-if="videoCall" v-bind:store="store"></WebRTCComponent>
</div>
</template>

<script>
import {
    ValidationProvider,
    extend,
    ValidationObserver
} from 'vee-validate';
import {
    required,
    email
} from 'vee-validate/dist/rules';
import CRMcolumn from "./CrmColmun"
import {
    getDateInFormat,
    dateFromat,
    secondsToHms
} from "../assets/common/vue-common"
import DatePicker from 'v-calendar/lib/components/date-picker.umd'
import WebRTCComponent from './WebRTCComponent.vue';
extend('email', email);
extend('required', required)
export default {
    name: "Calling",
    components: {
        ValidationProvider,
        ValidationObserver,
        DatePicker,
        WebRTCComponent
    },
    props: {
        store: {
            type: Object
        },
        phoneNumber: {
            type: String
        }
    },
    data() {
        return {
            dateMask: {
                input: dateFromat,
            },
            dialPadShow: false,
            dailNumber: '',
            makeCallPopupShow: false,
            callSatus: '',
            runningCallInformation: '',
            addNewContact: false,
            callComment: {
                callerName: '',
                comment: '',
                refSourceId: '',
                mappingType: '',
                isCancel: '',
                logId: ''
            },
            contactInfo: {
                txtFirstName: '',
                txtLastName: '',
                txtBusinessPhone: '',
                txtEmailId: '',
                txtMobilePhone: '',
                txtAddressLine3: '',
                country: '',
                Status: '',
                txtState: '',
                txtDateOfBirth: '',
                txtZipCode: '',
                txtCity: '',
                txtJobTitle: ''
            },
            blankcallComment: {},
            blankcontactInfo: {},
            crmTabSelect: 'Leads',
            activeTab: 'Ticket',
            logId: '',
            subModule: [],
            videoCall: false,
            muteUnmute: 'mute',
            //Device:null
        }
    },
    created() {
        //this.getToken()
        this.store.countryGet()
        this.store.moduleDrowdown()

    },

    mounted() {
        Object.assign(this.blankcallComment, this.callComment)
        Object.assign(this.blankcontactInfo, this.contactInfo)
        this.dailNumber = this.phoneNumber
        if (this.phoneNumber) {
            this.dialPadShow = true
            setTimeout(() => {
                this.makeOutgoingCall()
                this.store.contactInformation(`phone=${encodeURIComponent(this.dailNumber)}`)
                this.store.callTabsInformation({
                    params: `mobileNum=${encodeURIComponent(this.dailNumber)}`,
                    tab: "Ticket"
                })
            }, 1000)
        }
        //this.store.contactInformation(`phone=${encodeURIComponent('+91 82094 63215')}`)
    },
    sockets: {
        connect() {
            console.log('Connected to server');
        },
        disconnect() {
            console.log('Disconnected from server');
        },
        callingFrom: function (message) {
            console.log("callingFrom", message)

        },
        incomingCall: function (call) {
            console.warn(call)

        },
        audioPayload: function (message) {
            console.warn(message)

        },
        receiveCalling: function (message) {
            console.log("receiveCalling", message)

        },
        OutgoingCallFromIPphoneOut: function (message) {
            this.logId = message.item.LOG_ID
            console.log("OutgoingCallFromIPphoneOut", message)
        },
        call_status: function (message) {
            console.log("status", message.call)
            var vm = this;
            vm.callSatus = message.call.CallStatus
            vm.$parent.callStatus(vm.callSatus)
            var parmObject = {
                companyId: this.store.getUserInformation.CompanyId,
                Channel: '',
                extension: message.call.Caller,
                callFrom: message.call.Caller,
                callTo: message.call.To,
                callerIdName: '',
                CallType: message.call.From == process.env.VUE_APP_NUMBER ? 'Outgoing' : 'Incomming',
                QueryType: '',
                recentStatus: '',
                actionId: null,
                linkedId: message.call.ParentCallSid,
                uniqueId: message.call.CallSid,
                userId: this.store.getUserInformation.ID,
                logId: 0,
                callerImage: null,
                CallingMedium: 'twilio'
            };
            //initiated   ringing in-progress completed
            if (vm.callSatus == 'ringing') {
                console.log("RINGING", message.call)
                parmObject.recentStatus = 'RINGING'
                parmObject.QueryType = 'INSERT'
                vm.$socket.emit('incomingOutGoingRecord', parmObject)
            }
            if (vm.callSatus == 'in-progress') {
                vm.store.$state.callEnded = false
                parmObject.recentStatus = 'CALLSTART'
                parmObject.QueryType = 'UPDATE'
                parmObject.logId = vm.logId
                vm.$socket.emit('incomingOutGoingRecord', parmObject)
                vm.store.callTimerStart()
                vm.runningCallInformation = message.call
            }
            if (vm.callSatus == 'initiated') {
                //vm.$socket.emit('OutgoingCallFromIPphone',{to:vm.dailNumber})
            }
            if (vm.callSatus == 'completed') {
                vm.store.$state.incomingAndOutGoingCall = false
                var completeCall = {
                    logId: vm.logId,
                    QueryType: 'UPDATE',
                    recentStatus: 'HANGUP'
                }
                vm.$socket.emit('incomingOutGoingRecord', completeCall)
                if (vm.store.$state.callEnded) {
                    vm.store.$state.outGoingcallData = ''
                    vm.store.$state.incomingCallData = ''
                } else {
                    vm.hangup()
                }
            }

        },
        broadcastContact: function () {

        },
        newMessageForward: function () {
        }
    },
    methods: {

        // secondsToHms(secs) {
        //     if (secs) {
        //         var sec_num = parseInt(secs, 10)
        //         var hours = Math.floor(sec_num / 3600)
        //         var minutes = Math.floor(sec_num / 60) % 60
        //         var seconds = sec_num % 60

        //         return [hours, minutes, seconds]
        //             .map(v => v < 10 ? "0" + v : v)
        //             .filter((v, i) => v !== "00" || i > 0)
        //             .join(":")
        //     } else {
        //         return ''
        //     }
        // },
        getTime(date) {
            const newInputDate = date.replace(" ", "T");

            const timeStamp = new Date(newInputDate);
            return timeStamp.toLocaleTimeString(navigator.language, {
                hour: '2-digit',
                minute: '2-digit'
            })
        },

        //incoming call accept
        incomingCallPick(call) {
            this.store.$state.callEnded = false
            console.log("checkkk", this.store.getIncomingCallData.parameters.From)
            this.store.contactInformation(`phone=${encodeURIComponent(this.store.getIncomingCallData.parameters.From)}`)
            this.dailNumber = this.store.getIncomingCallData.parameters.From
            this.store.clearInterval()
            this.store.$state.outGoingcallData = ''
            call.accept()
            this.incomingCall = false
            // device.updateToken(token)
        }, 

        async makeOutgoingCall() {
            var params = {
                To: this.dailNumber,
            };
            this.store.makeOutgoingCall(params)
        },
        rejectCall(event) {
            event.reject()
        },
      
        callMute() {
            this.muteUnmute = 'unmute'
            let callRunningData = this.store.getIncomingCallData ? this.store.getIncomingCallData : this.store.getOutGoingcallData
            callRunningData.mute(true);
        },
        callUnmute() {
            this.muteUnmute = 'mute'
            let callRunningData = this.store.getIncomingCallData ? this.store.getIncomingCallData : this.store.getOutGoingcallData
            callRunningData.mute(false);
        },
        updateUIAcceptedOutgoingCall() {
            //this.callSatus = "in-progress"
        },
        updateUIDisconnectedOutgoingCall() {
            //this.callSatus = "disconnect"
        },
        async getToken() {
            var vm = this;
            var Orte = ['first', 'second', 'third', 'user', 'callUser']
            var randomWord = Orte[Math.random() * Orte.length | 0]
            await vm.$socket.emit('getToken', {
                identity: randomWord,
                number: process.env.VUE_APP_NUMBER
            }, function (deviceToken) {
                vm.store.$state.callToken = deviceToken.deviceToken
                vm.store.intitializeDevice()
            });
        },
        hangup() {
            this.store.hangup()
        },
        makeCall() {
                if (this.dailNumber) {
                    this.makeCallPopupShow = true
                }
        },
        redirectCall(event) {
            if (this.store.$state.callToken) {
                this.dailNumber = event
                 this.makeCallPopupShow = true
            } else {
                this.ShowAlert(this.$t("ConnectDevice"), "failure", true, this.$t("Alert"));
            }
            
        },
        addString(event) {
            if (event == 'X') {
                if (this.dailNumber.length > 0) {
                    this.dailNumber = this.dailNumber.split("").reverse().slice(1).reverse().join("")
                }
            } else {
                this.dailNumber += event
            }
        },
        isNumber(evt) {
            evt = (evt) ? evt : window.event;
            var charCode = (evt.which) ? evt.which : evt.keyCode;
            if ((charCode > 31 && (charCode < 48 || charCode > 57))) {
                evt.preventDefault();
            } else {
                return true;
            }

        },

        async showDailPaid() {
            var vm = this;

            vm.dialPadShow = true
        },
        callhistory() {
            this.dialPadShow = false
        },
        //submoduleDropdown
        getSubmodule(event) {
            this.callComment.mappingType = ''
            console.log("event", event)
            if (event) {
                this.subModule = event.SUB_MODULES ? JSON.parse(event.SUB_MODULES) : []
            } else {
                this.subModule = []
            }
        },
        async calling(event) {
            var vm = this;

            vm.makeCallPopupShow = false
            if (event == 'nexo') {
                vm.store.NexoCall(`UserId=${vm.store.getUserInformation.ID}&CompanyId=23232&PhoneNumber=${vm.dailNumber}`)
            } else if (event == 'asterisk') {
                vm.store.NexoCall(`UserId=${vm.store.getUserInformation.ID}&CompanyId=23232&PhoneNumber=${vm.dailNumber}`)
            } else if (event == 'vonage') {
                let obj = {
                    UserId: vm.store.getUserInformation.ID,
                    CompanyId: '353453',
                    PhoneNumber: vm.dailNumber
                }
                vm.store.VonageCall(obj)
            } else if (event == 'twilio') {
                if (vm.store.$state.callToken) {
                    vm.makeOutgoingCall()
                vm.$parent.numberDail(vm.dailNumber)
                vm.store.contactInformation(`phone=${encodeURIComponent(vm.dailNumber)}`)
                vm.store.callTabsInformation({
                    params: `mobileNum=${encodeURIComponent(vm.dailNumber)}`,
                    tab: "Ticket"
                })
            } else {
                vm.ShowAlert(vm.$t("ConnectDevice"), "failure", true, vm.$t("Alert"));
            }
                
                //vm.$socket.emit('makeCall',{to:vm.dailNumber})
            } else if (event == 'videoCall') {
                vm.videoCall = true
            }

        },
        clearAllState() {
            this.store.$state.callEnded = false
            this.callSatus = ''
            this.store.$state.timerStart = 0
            this.runningCallInformation = ''
            this.dailNumber = ''
            this.store.$state.incomingcallNumber = ''
            console.log("checkk", this.blankcallComment)
            Object.assign(this.callComment, this.blankcallComment)

        },
        cancelCallComment() {
            this.clearAllState()
            this.dialPadShow = true
        },
        //runnning call comment submit
        async formSubmit() {
            let obj = {}
            Object.assign(obj, this.callComment)
            obj.refSourceId = obj.refSourceId ? obj.refSourceId.MODULE_ID : obj.refSourceId
            obj.mappingType = obj.mappingType ? obj.mappingType.SUB_MODULE_NAME : obj.mappingType
            obj.logId = Number(this.logId)

            await this.store.addCommentForCall(obj)
            console.log("getAddCommentForCallRes", this.store.getAddCommentForCallRes.StatusMessage)
            if (this.store.getAddCommentForCallRes.StatusMessage == "Success") {

                this.dialPadShow = true
                this.$refs.commentForm.reset()
                this.subModule = []
                this.clearAllState()
                // this.callComment.callerName=  '',
                // this.callComment.comment= '',
                // this.callComment.refSourceId= '',
                // this.callComment.mappingType= '',
                // this.callComment.isCancel= '',
                // this.callComment.logId= ''
                this.ShowAlert(this.$t(this.store.getAddCommentForCallRes.StatusMessage), "success", true, this.$t('Alert'));
            } else {
                this.ShowAlert(this.$t(this.store.getAddCommentForCallRes.StatusMessage), "danger", true, this.$t('Alert'));
            }
        },
        //New contact Informatio submit
        async contactSubmit() {
            let contactObj = {}
            Object.assign(contactObj, this.contactInfo)
            contactObj.txtBusinessPhone = this.contactInfo.txtBusinessPhone
            contactObj.txtMobilePhone = this.contactInfo.txtMobilePhone
            contactObj.country = this.contactInfo.country ? this.contactInfo.country.value : ''
            await this.store.addContactInformation(contactObj)
            if (this.store.getResponseSubmitContactInfo.StatusMessage == "Success") {
                this.store.contactInformation(`phone=${encodeURIComponent(this.contactInfo.txtMobilePhone)}`)
                this.addNewContact = false
                this.ShowAlert(this.$t(this.store.getResponseSubmitContactInfo.StatusMessage), "success", true, this.$t('Alert'));
                this.$refs.contactForm.reset()
                Object.assign(this.contactInfo, this.blankcontactInfo)
            } else {
                this.ShowAlert(this.$t(this.store.getResponseSubmitContactInfo.StatusMessage), "danger", true, this.$t('Alert'));
            }
            // this.callSatus = ''
            // this.timerStart=0
            // this.runningCallInformation = ''
        },
        //current active tab
        tabSelect(event) {
            this.activeTab = event
            this.dailNumber = this.store.$state.incomingcallNumber ? this.store.$state.incomingcallNumber : this.dailNumber
            if (event == 'crm') {
                this.store.crmDetailRecord({
                    params: `contactId=${this.store.getContactDetails.contact_id}`,
                    tab: 'Leads'
                })
            } else {
                this.dailNumber = this.store.getIncomingCallData ? this.store.getIncomingCallData.parameters.From : this.dailNumber
                this.store.callTabsInformation({
                    params: `mobileNum=${encodeURIComponent(this.dailNumber)}`,
                    tab: event
                })
            }

        },
        //CRM child tab Details
        getOrderDetails(event) {
            this.crmTabSelect = event
            this.store.crmDetailRecord({
                params: `contactId=${this.store.getContactDetails.contact_id}`,
                tab: event
            })
        },
        showAddNewContactForm() {
            this.contactInfo.txtMobilePhone = this.dailNumber
            this.addNewContact = true
        },
        viewRecords(id) {
            console.log("id", id)
        },

        forwordCall() {
            let callRunningData = this.store.getIncomingCallData ? this.store.getIncomingCallData : this.store.getOutGoingcallData
      if (callRunningData) {
        this.$socket.emit('transfer', {
            To:this.dailNumber
            }, function (deviceToken) {
                console.log('Call transferred',deviceToken);
            })
        }
    },
    startConference() {
        this.$socket.emit('conference', function (record) {
                console.log('Call transferred',record);
            })
    },

    },
    setup() {
        return {
            CRMcolumn,
            getDateInFormat,
            secondsToHms
        }
    }
}
</script>
